.mypage {

  .main {
    display: flex;
  }

  .notes {
    margin: 0 auto;
    height: 100vh;
    width: 60%;

    ul {
      display: flex;
      justify-content: space-between;
      margin-top: 5%;
      margin-bottom: 5%;
      border-radius: 10px;
      border: 1px solid #ccc;
    }

    li {
      width: 150px;
      text-align: center;
      font-size: 17px;

      button {
        width: 85%;
        background-color: white;
        padding: 20px;
        font-weight: 700;

        &:hover {
          color: #ff0000;
          border-bottom: 3px solid #ff0000;
        }
      }

      .select {
        color: #ff0000;
        border-bottom: 3px solid #ff0000;
      }
    }

  }

  .user-data {
    padding: 10px;
    border-bottom: 1px solid #ccc;

    &:last-child {
      border-bottom: none;
    }

    .title {
      cursor: pointer;
    }
  }


  .title {
    margin-bottom: 10px;
    font-size: 30px;
    font-weight: 500;
    left: 0;
  }

  .content {
    left: 0;
    margin-bottom: 10px;
    font-size: 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .nickname {
    left: 0;
    color: #797979;
  }
}