.center {
  margin-top: 10%;
  text-align: center;
}

.headername {
  p {
    font-size: 30px;
    font-weight: 700;
  }
}

.line-style {
  display: flex;
  align-items: center;
  justify-content: center;

  .jb-division-line {
    width: 130px;
    margin: 10px;
    border-top: 1px solid #a4a4a4;
  }
}


.prople {
  margin: 20px auto;
  height: 200px;
  width: 200px;
  border-radius: 30px;

  .imgwrap {
    border: 2px solid #d1d1d1;
    position: relative;
    height: 200px;
    overflow: hidden;
    box-sizing: border-box;
    border-radius: 12px;
    cursor: pointer;

    input[type="file"] {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      padding: 0;
      cursor: pointer;
      opacity: 0;
    }
  }

  img {
    position: absolute;
    // top: -1000%;
    // right: -1000%;
    // bottom: -1000%;
    // left: -1000%;
    // margin: auto;
    // height: 100%;
    // padding: 20px;
    // width: 100%;
    // height: 220px;
    // box-sizing: border-box;
  }
}

.gaider {
  margin: 50px auto;
  padding: 10px;
  width: 350px;
  border-radius: 10px;
  background: #ececec;
  box-sizing: border-box;
  font-size: 12px;
}

.registIDform {
  width: 60%;
  padding: 10px;
  margin: 0 auto;
}


.emailmodule {

  //이메일
  .disable {
    background-color: #f1f1f1;
    color: #777777;
    cursor: not-allowed;
  }

  label {
    display: inline-block;
    width: 100%;
    text-align: left;
    margin-top: 20px;
  }

  input,
  select {
    width: 100%;
    height: 50px;
    border-radius: 10px;
    margin: 0;
    padding: 0;
    border: 2px solid #d1d1d1;
    box-sizing: border-box;
  }

}


.tagmodule {

  //테그
  label {
    text-align: left;
  }

  .tagalign {
    margin: 30px 0;
  }

}

.labelmodule {

  // 닉네임
  .labeltitle {
    width: 100%;
    text-align: left;

    align-items: center;
    align-content: center;
    vertical-align: middle;

    label {
      display: inline-block;
      margin-top: 20px;
    }

    .star {
      color: #f11616;
    }
  }

  .inputcont {
    display: flex;

    input {
      width: 100%;
      height: 50px;
      margin-right: 20px;
      border-radius: 10px;
      border: 2px solid #d1d1d1;
      box-sizing: border-box;
    }
  }
}

.description {
  label {
    text-align: left;
  }

}

.loginbutton {
  text-align: center;
}